import styled from 'styled-components'
import { down } from 'styled-breakpoints'
interface IIconProps {
  type: string
}

export const Container = styled.div`
  height: 42px;
  line-height: 42px;
  cursor: pointer;
  color: ${props => props.theme.palette.secondary};
  font-weight: ${props => props.theme.font.weight.m};
  transition: all 300ms ease;
  font-size: 22px;
  ${down('md')} {
    height: auto;
  }
  &:hover > span {
    color: ${props => props.theme.palette.middlePrimary};
    border-bottom: 5px solid ${props => props.theme.palette.middlePrimary};
  }
`
export const ToggleIcon = styled.div<IIconProps>`
  width: 42px;
  min-width: 42px;
  height: 42px;
  background: ${props =>
    props.type === 'light'
      ? props.theme.palette.lightSecondary
      : props.theme.palette.lightPrimary};
  color: ${props => props.theme.palette.secondary};
  display: flex;
  transition: all 300ms ease;
  svg {
    margin: auto;
  }
  ${Container}:hover & {
    background: ${props => props.theme.palette.middlePrimary};
  }
`
