import React from 'react'
import { v4 as uuidv4 } from 'uuid'

import {
  Container,
  Arrow,
  CustomLink,
  SubMenu,
  Wrapper,
  CustomButton,
} from './style'
import { navigate } from 'gatsby'
interface IMenuButtonProps {
  to: string
  color: string
  isFr: boolean
  className?: string
  subMenu: {
    url: string
    color: string
    title: string
    titleFr: string
  }[]
  children: React.ReactNode
  onClick?(): void
}

export default function MenuButton({
  to,
  children,
  color,
  className,
  subMenu,
  isFr,
  onClick,
}: IMenuButtonProps) {
  return (
    <Wrapper>
      <Container background={color} className={className}>
        <CustomLink onClick={() => onClick()} to={to} replace>
          {children}
        </CustomLink>

        <Arrow type={subMenu.length === 0 ? 'none' : 'left'} />
      </Container>
      <SubMenu
        hasSubMenu={subMenu.length > 0}
        size={subMenu.length} 
        // visible={expanded && subMenu.length > 0}
      >
        {subMenu.map(({ color, title, titleFr, url }) => {
          return (
            <Container
              background={color}
              key={uuidv4()}
              className='mt-3 mt-sm-4'
            >
              <CustomLink to={isFr ? '/fr' + url : url} onClick={onClick}>
                {isFr ? titleFr : title}
              </CustomLink>
            </Container>
          )
        })}
      </SubMenu>
    </Wrapper>
  )
}
