import styled from 'styled-components'
import { down } from 'styled-breakpoints'
import { Link } from 'gatsby'

interface IContainerProps {
  background: string
}

export const CustomButton = styled.div`
  width: 100%;
  font-size: 32px;
  line-height: ${props => props.theme.font.size.xxl};
  color: white;
  &:hover {
    text-decoration: none;
    color: white;
  }
  ${down('lg')} {
    /* height: 60px; */
    padding: 5px 0;
    font-size: ${props => props.theme.font.size.l};
    line-height: ${props => props.theme.font.size.l};
    &:hover {
      background: transparent;
      padding: 7px 0;
      color: white;
      text-decoration: none;
    }
  }

  @media screen and (max-width: 320px) {
    font-size: ${props => props.theme.font.size.m};
  }
`
export const CustomLink = styled(Link)`
  width: 100%;
  font-size: 32px;
  line-height: ${props => props.theme.font.size.xxl};
  color: white;
  &:hover {
    text-decoration: none;
    color: white;
  }
  ${down('lg')} {
    /* height: 60px; */
    padding: 5px 0;
    font-size: ${props => props.theme.font.size.l};
    line-height: ${props => props.theme.font.size.l};
    &:hover {
      background: transparent;
      padding: 7px 0;
      color: white;
      text-decoration: none;
    }
  }

  @media screen and (max-width: 320px) {
    font-size: ${props => props.theme.font.size.m};
  }
`
export const Container = styled.div<IContainerProps>`
  width: 500px;
  font-weight: ${props => props.theme.font.weight.l};
  text-align: center;
  position: relative;
  letter-spacing: 0.13px;
  background: ${props => props.background};
  box-shadow: 0px 3px 6px #00000029;
  border-top-left-radius: 50px;
  border-bottom-left-radius: 0px;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 50px;
  box-sizing: border-box;
  border: 9px solid ${props => props.background};
  transition: all 300ms ease;
  padding: 7px 0;
  /* height: 80px; */
  display: flex;
  &:hover {
    border: 9px solid ${props => props.background};
    background: transparent;
    padding: 7px 0;
    color: white;
    text-decoration: none;
  }

  ${down('md')} {
    width: 100%;
  }

  @media screen and (max-width: 320px) {
    border: 4px solid ${props => props.background};
    font-size: ${props => props.theme.font.size.xs};
    padding: 1px 0;
    border-top-left-radius: 30px;
    border-bottom-left-radius: 0px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 30px;
    &:hover {
      border: 4px solid ${props => props.background};
      background: transparent;
      padding: 0px 0;
      color: white;
      text-decoration: none;
    }
  }
`

interface IArrow {
  type: string
}
export const Arrow = styled.div<IArrow>`
  right: 30px;
  margin-top: auto;
  margin-bottom: auto;
  position: absolute;
  cursor: pointer;
  transition: all 300ms ease;

  background-color: white;

  top: ${props =>
    props.type === 'none' ? '0px' : props.type === 'left' ? '14px' : '20px'};
  width: ${props =>
    props.type === 'none' ? '0px' : props.type === 'left' ? '26px' : '30px'};
  height: ${props =>
    props.type === 'none' ? '0px' : props.type === 'left' ? '30px' : '26px'};
  clip-path: ${props =>
    props.type === 'none'
      ? 'polygon(0% 0, 0 0, 0% 0%)'
      : props.type === 'left'
      ? 'polygon(100% 100%, 100% 0, 0 50%)'
      : 'polygon(100% 0, 0 0, 50% 100%)'};
  ${Container}:hover > & {
    transform: rotate(-90deg);
    transition: 300ms ease all;
  }

  ${down('lg')} {
    right: 20px;
    top: ${props =>
      props.type === 'none' ? '0px' : props.type === 'left' ? '10px' : '14px'};
    width: ${props =>
      props.type === 'none' ? '0px' : props.type === 'left' ? '21px' : '24px'};
    height: ${props =>
      props.type === 'none' ? '0px' : props.type === 'left' ? '24px' : '21px'};
  }
  @media screen and (max-width: 320px) {
    right: 16px;
    top: ${props =>
      props.type === 'none' ? '0px' : props.type === 'left' ? '9px' : '9px'};
    width: ${props =>
      props.type === 'none' ? '0px' : props.type === 'left' ? '14px' : '18px'};
    height: ${props =>
      props.type === 'none' ? '0px' : props.type === 'left' ? '18px' : '14px'};
  }
`
export const Wrapper = styled.div``
interface ISubMenuProps {
  visible: boolean
  hasSubMenu: boolean
  size: number
}
export const SubMenu = styled.div<ISubMenuProps>`
  max-height: ${props => (props.visible ? `${110 * props.size}px` : 0)};
  height: ${props => (props.hasSubMenu ? `${110 * props.size}px` : 0)};
  overflow: hidden;
  transition: all 300ms ease;

  ${Wrapper}:hover > & {
    max-height: ${props => (props.hasSubMenu ? `${110 * props.size}px` : "200px")};
  }

  ${down('lg')} {
    max-height: ${props => (props.visible ? `${90 * props.size}px` : 0)};
    height: ${props => (props.hasSubMenu ? `${90 * props.size}px` : 0)};
  }
  @media screen and (max-width: 320px) {
    height: auto;
  }
`
