import React, { useState } from 'react'
import { Link } from 'gatsby'
import Menu from './Menu'
import { HamburgerButton } from './Menu/style'
import { useLocation } from '@reach/router'
import LogoImg from './../../images/logo.svg'
import LogoImgFR from './../../images/logoFR.svg'
import HamburgerImg from './../../images/hamburger.svg'
import { StyledHeader } from './style'
export default function Header() {
  const [isMenuOpen, setIsMenuOpen] = useState(false)
  const location = useLocation()
  const isFr = location.pathname.startsWith('/fr/')
  return (
    <StyledHeader className='w-100'>
      <div className='container d-flex w-100 h-100 justify-content-between'>
        <Link to={isFr ? '/fr/' : '/'}>
          <HamburgerButton src={isFr ? LogoImgFR : LogoImg}  alt={isFr ? 'Génération pour l’Égalité (Logo)' : 'Generations 4 Equality (Logo)'} />
        </Link>
        <a href='#'>
          <HamburgerButton
            src={HamburgerImg}
            alt='Menu'
            onClick={() => setIsMenuOpen(true)}
            className='cursor-pointer'
          />
        </a>
      </div>
      <Menu open={isMenuOpen} onClose={() => setIsMenuOpen(false)} />
    </StyledHeader>
  )
}
