import React, { useState, useContext, useEffect } from 'react'
import { Link } from 'gatsby'
import { useLocation } from '@reach/router'
import { LangContext } from './../../../layout'
import { Container, Language, HamburgerButton, SubMenu } from './style'
import { MenuButton, SocialIcon } from './../../../components'
import CloseIcon from './../../../images/hamburger-close.svg'
import LogoImg from './../../../images/logo-only.svg'
import { v4 as uuidv4 } from 'uuid'

interface IMenuProps {
  open: boolean
  onClose(): void
}

interface ICurrentMenu {
  title: string
  titleFr: string
  url: string
  color: string
  children: {
    title: string
    titleFr: string
    url: string
    color: string
  }[]
}

export default function Menu({ open, onClose }: IMenuProps) {
  const [currentMenu, setCurrentMenu] = useState<ICurrentMenu[]>(menuData)

  const location = useLocation()
  const { lang } = useContext(LangContext)
  const handleClick = (title: string) => {
    let temp = currentMenu
    const currentIndex = temp.findIndex(d => d.title === title)
    temp[currentIndex].expanded = !currentMenu[currentIndex].expanded
    temp = temp.map((d, index) => {
      if (index !== currentIndex) return { ...d, expanded: false }
      else return d
    })
    console.log('select')
    setCurrentMenu(temp)
    onClose()
  }

  return (
    <Container open={open}>
      <div className='container h-100'>
        <div className='d-flex h-100'>
          <Link className='logo my-auto' to='/'>
            <img src={LogoImg} alt='logo' />
          </Link>
          <div className='right-section ml-0 ml-md-auto d-flex flex-column'>
            <a href='#' className='ml-auto'>
              <HamburgerButton src={CloseIcon} alt='close' onClick={onClose} />
            </a>
            <div className='mb-auto'>
              {currentMenu.map(
                ({ color, url, title, titleFr, children }: ICurrentMenu) => {
                  return (
                    <MenuButton
                      isFr={lang === 'fr'}
                      to={lang === 'fr' ? '/fr' + url : url}
                      key={uuidv4()}
                      className='mt-sm-4 mt-3'
                      color={color}
                      subMenu={children}
                      onClick={() => handleClick(title)}
                    >
                      {lang === 'fr' ? titleFr : title}
                    </MenuButton>
                  )
                }
              )}
            </div>
            <div className='d-block d-md-flex mt-auto mb-4'>
              <Link
                to={
                  lang === 'fr'
                    ? location.pathname.slice(3)
                    : '/fr' + location.pathname
                }
              >
                <Language className='my-auto mx-auto mx-md-0'>
                  {lang === 'fr' ? 'English' : 'Français'}{' '}
                </Language>
              </Link>
              <div className='d-flex ml-auto mr-auto mr-md-0 w-fit mt-3 mt-sm-4 mt-md-0'>
                <SocialIcon
                  type='twitter'
                  to='https://twitter.com'
                  className='ml-auto'
                />
                <SocialIcon
                  type='instagram'
                  to='https://instagram.com'
                  className='ml-3'
                />
                <SocialIcon
                  type='facebook'
                  to='https://facebook.com'
                  className='ml-3'
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </Container>
  )
}

