import React from 'react'
import { Container, Segment } from './style'

interface IStripeBarProps {
  colors?: string[]
}

export default function StripeBar({
  colors = ['#387896', '#95c5bb', '#d1932a', '#d85327'],
}: IStripeBarProps) {
  return (
    <Container className='d-flex w-100'>
      {colors.map((color, index) => {
        return <Segment className='w-25' color={color} key={`${index}key`} />
      })}
    </Container>
  )
}
