import React, { useState } from "react";
import { Accordion } from "react-bootstrap";
import { Button } from "../../components";
import { FaPlus } from "react-icons/fa";
import ReactHTMLParser from "react-html-parser";

import {
  ToggleIcon,
  AccordionItemHeader,
  AccordionItem,
  Content,
  AccordionItemHeaderContent,
  CallToActionContainer
} from "./style";

interface IAccordionProps {
  background?: string;
  className?: string;
  data: {
    title: string;
    content: string;
    callToActionUrl: string;
    callToActionText: string;
    callToActionIsExternal?: boolean;
  }[];
}

interface ICurrent {
  title: string;
}

export default ({ data, className, background = "light" }: IAccordionProps) => {
  const [current, setCurrent] = useState<ICurrent>({ title: "" });

  return (
    <div className={className}>
      <Accordion defaultActiveKey="0">
        {data.map((item, index) => {
          return (
            <AccordionItem key={`${item.title}-${index}-key`}>
              <Accordion.Toggle
                as={AccordionItemHeader}
                onClick={() =>
                  setCurrent(
                    current.title === item.title ? { title: "" } : item
                  )
                }
                eventKey={`${index + 1}`}
              >
                <ToggleIcon
                  type={background}
                  active={current.title === item.title}
                >
                  <FaPlus size={24} />
                </ToggleIcon>
                <AccordionItemHeaderContent
                  active={current === item}
                  className="ml-2"
                >
                  <span>{item.title}</span>
                </AccordionItemHeaderContent>
              </Accordion.Toggle>

              <Accordion.Collapse eventKey={`${index + 1}`}>
                <Content>
                  {item.content}

                  {item.callToActionUrl && (
                    <CallToActionContainer>
                      <Button
                        type="url"
                        urlType={item.callToActionIsExternal ? "external" : "internal"}
                        background="light"
                        to={item.callToActionUrl}
                      >
                        {item.callToActionText ? item.callToActionText : item.title}
                      </Button>
                    </CallToActionContainer>               
                      
                    
                  )}
                </Content>
              </Accordion.Collapse>
            </AccordionItem>
          );
        })}
      </Accordion>
    </div>
  );
};
