import React from 'react'
import { FaTwitter, FaInstagram, FaFacebookF } from 'react-icons/fa'
import { Container } from './style'

interface ISocialIconProps {
  to: string
  type: string
  className?: string
}

export default function SocialIcon({ to, type, className }: ISocialIconProps) {
  return (
    <Container href={to} className={className} target='_blank'>
      {type === 'twitter' ? (
        <FaTwitter size={32} />
      ) : type === 'instagram' ? (
        <FaInstagram size={32} />
      ) : type === 'facebook' ? (
        <FaFacebookF size={32} />
      ) : null}
    </Container>
  )
}
