import React from 'react'
import { useLocation } from '@reach/router'
import { StyledButton, StyledLink, Anchor } from './style'
interface IButtonProps {
  type?: 'button' | 'url' | ''
  buttonType?: 'button' | 'submit' | ''
  background?: string
  urlType?: 'external' | 'internal' | ''
  children: React.ReactNode
  to?: string
  className?: string
  onClick?(): void
}

export default function Button({
  type = 'button',
  urlType = 'internal',
  background = 'dark',
  buttonType = 'button',
  className,
  children,
  to = '/',
  onClick,
}: IButtonProps) {
  const location = useLocation()
  const isFr = location.pathname.startsWith('/fr/')

  return (
    <>
      {type === 'button' ? (
        <StyledButton
          background={background}
          className={className}
          onClick={onClick}
          type={buttonType}
        >
          {children}
        </StyledButton>
      ) : urlType === 'internal' ? (
        <StyledLink
          background={background}
          className={className}
          to={isFr ? `/fr${to}` : to}
        >
          {children}
        </StyledLink>
      ) : (
        <Anchor
          background={background}
          className={className}
          href={to}
          target='_blank'
        >
          {children}
        </Anchor>
      )}
    </>
  )
}
