import React from 'react'
import { Container } from './style'

interface IHomeOthersProps {
  underline?: string
  className?: string
  to: string
  children: React.ReactNode
}

export default function CustomLink({
  underline = 'bold',
  to,
  children,
  className,
  ...props
}: IHomeOthersProps) {
  return (
    <Container href={to} className={className} underline={underline} {...props}>
      {children}
    </Container>
  )
}
