import styled from 'styled-components'
import { down } from 'styled-breakpoints'

interface IProps {
  underline: string
}

export const Container = styled.a<IProps>`
  color: ${props =>
    props.underline === 'bold'
      ? props.theme.palette.lightPrimary
      : props.theme.palette.primary};
  transition: all 300ms ease;
  text-decoration: none;
  display: block;
  position: relative;
  width: fit-content;
  font-weight: ${props => props.theme.font.weight.l};
  font-size: ${props =>
    props.underline === 'bold'
      ? props.theme.font.size.xm
      : props.theme.font.size.m};
  line-height: ${props => (props.underline === 'bold' ? '24px' : '20px')};
  &:before {
    content: '';
    position: absolute;
    width: 0;
    height: ${props => (props.underline === 'bold' ? '4px' : '2px')};
    bottom: -4px;
    left: 0;
    background-color: ${props =>
      props.underline === 'bold'
        ? props.theme.palette.lightPrimary
        : props.theme.palette.primary};
    visibility: hidden;
    transition: all 0.3s ease-in-out;
  }
  &:hover:before {
    visibility: visible;
    width: 100%;
  }
  &:hover {
    color: ${props =>
      props.underline === 'bold'
        ? props.theme.palette.lightPrimary
        : props.theme.palette.primary};
    text-decoration: none;
  }
`
