import React from 'react'
import { useField } from 'formik'
import { useLocation } from '@reach/router'
import { Label, Input, Container, Validation } from './style'

interface IFormInputProps {
  label?: string
  labelFr?: string
  labelColor?: string
  name: string
  className?: string
  type?: string
  required?: boolean
}

export default ({
  label,
  labelFr,
  className,
  labelColor = '#00587f',
  type = 'text',
  required = false,
  ...props
}: IFormInputProps) => {
  const [field, meta] = useField(props)
  const loc = useLocation()
  const isFr = loc.pathname.startsWith('/fr/')

  return (
    <Container className={className}>
      {label && !isFr && (
        <Label color={labelColor}>
          {label} {required && <span>*</span>}
        </Label>
      )}
      {labelFr && isFr && (
        <Label color={labelColor}>
          {labelFr} {required && <span>*</span>}
        </Label>
      )}
      <Input
        {...props}
        {...field}
        type={type}
        error={(meta.touched && meta.error) as boolean}
      />
      <Validation>{meta.error}</Validation>
    </Container>
  )
}
