import styled from 'styled-components'
import { Link } from 'gatsby'

interface IProps {
  background: string
}

export const StyledButton = styled.button<IProps>`
  border-radius: 31px;
  padding: 7px 31px;
  color: ${props => props.theme.palette.primary} !important;
  font-size: ${props => props.theme.font.size.m};
  line-height: 28px;
  background: ${props => props.theme.palette.lightPrimary};
  font-weight: ${props => props.theme.font.weight.xl};
  width: fit-content;
  outline: none;
  border: 9px solid ${props => props.theme.palette.lightPrimary};
  box-sizing: border-box;
  transition: all 300ms ease;
  outline: none;
  text-align: center;
  &:hover {
    transition: all 300ms ease;
    background: transparent;
    padding: 7px 31px;
    color: ${props =>
      props.background === 'dark' ? 'white' : props.theme.palette.primary};
    border: 9px solid ${props => props.theme.palette.lightPrimary};
    box-sizing: border-box;
  }
  &:focus,
  &:active {
    outline: none;
  }
`

export const StyledLink = styled(Link)<IProps>`
  border-radius: 31px;
  padding: 7px 31px;
  color: ${props => props.theme.palette.primary} !important;
  font-size: ${props => props.theme.font.size.m};
  line-height: 28px;
  background: ${props => props.theme.palette.lightPrimary};
  font-weight: ${props => props.theme.font.weight.xl};
  width: fit-content;
  outline: none;
  border: 9px solid ${props => props.theme.palette.lightPrimary};
  height: auto;
  transition: all 300ms ease;
  display: block;
  text-align: center;
  &:hover {
    transition: all 300ms ease;
    background: transparent;
    padding: 7px 31px;
    color: ${props =>
      props.background === 'dark' ? 'white' : props.theme.palette.primary};
    border: 9px solid ${props => props.theme.palette.lightPrimary};
    box-sizing: border-box;
    text-decoration: none;
  }
  &:focus,
  &:active {
    outline: none;
  }
`
export const Anchor = styled.a<IProps>`
  border-radius: 31px;
  padding: 7px 31px;
  max-width: 100%;
  color: ${props => props.theme.palette.primary} !important;
  font-size: ${props => props.theme.font.size.m};
  line-height: 28px;
  background: ${props => props.theme.palette.lightPrimary};
  font-weight: ${props => props.theme.font.weight.xl};
  width: fit-content;
  outline: none;
  border: 9px solid ${props => props.theme.palette.lightPrimary};
  height: auto;
  transition: all 300ms ease;
  display: block;
  text-align: center;
  &:hover {
    transition: all 300ms ease;
    background: transparent;
    padding: 7px 31px;
    color: ${props =>
      props.background === 'dark' ? 'white' : props.theme.palette.primary};
    border: 9px solid ${props => props.theme.palette.lightPrimary};
    box-sizing: border-box;
    text-decoration: none;
  }
  &:focus,
  &:active {
    outline: none;
  }
`
