import React from 'react'

import { Container, Back, Half } from './style'

interface ICircleProps {
  colors?: string[]
  className?: string
  children: React.ReactNode
}

export default ({
  colors = ['#00587F', '#387896'],
  children,
  className,
}: ICircleProps) => {
  return (
    <Container className={className}>
      <Back className='position-absolute w-100 h-100'>
        <Half background={colors[0]} />
        <Half background={colors[1]} />
      </Back>
      <div className='m-auto'>{children}</div>
    </Container>
  )
}
