import React, { useState } from 'react'
import { useField } from 'formik'
import { useLocation } from '@reach/router'
import { Label, FileUpload, Container, Validation } from './style'
interface IProps {
  label: string
  className: string
}
export default ({ label, text, className, ...props }: IProps) => {
  const [field, meta, helpers] = useField(props)
  const [error, setError] = useState('')
  const loc = useLocation()
  const isFr = loc.pathname.startsWith('/fr/')
  let reader
  const selectFile = (event: any) => {
    setError('')
    const file = event.target.files[0]
    reader = new FileReader()
    if (file && file.type.match('image.*')) {
      reader.readAsDataURL(file)
      setError('')
    } else {
      setError(
        isFr
          ? 'Une image est nécessaire (jpeg, png, svg).'
          : 'An image file is required (jpeg, png, svg). '
      )
      helpers.setValue(null)
    }
    reader.onload = function(e: any) {
      helpers.setValue({ file: e.target.result, name: file.name })
    }
  }
  return (
    <Container className={className}>
      <Label>{label}</Label>
      <div className='d-flex'>
        <FileUpload>
          <input type='file' id='upload' onChange={selectFile} />
          <label htmlFor='upload'>{text}</label>
        </FileUpload>
        <div className='d-flex'>
          <span className='ml-3 my-auto'>{field.value?.name}</span>
        </div>
      </div>

      {/* <ProgressBar className='mt-4' value={progress} /> */}
      <Validation visible={meta.touched && meta.error && error}>
        {meta.error || error}
      </Validation>
    </Container>
  )
}
