import styled from 'styled-components'
import { down } from 'styled-breakpoints'

interface IContainerProps {
  colors: string[]
}

export const Container = styled.div<IContainerProps>`
  min-height: 480px;
  background: ${props => props.colors[0]};
  position: relative;
  color: white;
  padding: 40px 0;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: ${props => props.colors[1]};
    clip-path: polygon(60% 0, 100% 0, 100% 100%, 55% 100%);
  }
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: ${props => props.colors[2]};
    clip-path: polygon(80% 0, 100% 0, 100% 100%, 85% 100%);
  }

  ${down('md')} {
    height: auto;
    &:before {
      display: none;
    }
    &:after {
      display: none;
    }
  }
`
