import React from 'react'
import { useField } from 'formik'
import { Label, Textarea, Container, Validation } from './style'

interface IFormInputProps {
  label?: string
  className?: string
  name: string
  required?: boolean
}

export default ({
  label,
  className,
  required = false,
  ...props
}: IFormInputProps) => {
  const [field, meta] = useField(props)

  return (
    <Container className={className}>
      {label && (
        <Label>
          {label} {required && <span>*</span>}
        </Label>
      )}
      <Textarea {...props} {...field} error={meta.touched && meta.error} />
      <Validation visible={meta.touched && meta.error}>{meta.error}</Validation>
    </Container>
  )
}
