import React, { useRef, useEffect } from 'react'
import { useField } from 'formik'
import { useLocation } from '@reach/router'
import ReCAPTCHA from 'react-google-recaptcha'
import styled from 'styled-components'

interface IFormInputProps {
  label?: string
  labelFr?: string
  labelColor?: string
  name: string
  className?: string
  type?: string
  required?: boolean
}

export default ({ ...props }: IFormInputProps) => {
  const [field, meta, helpers] = useField(props)
  const loc = useLocation()
  const isFr = loc.pathname.startsWith('/fr/')
  const recaptchaRef = useRef()
  useEffect(() => {
    if (!field.value && recaptchaRef) recaptchaRef.current.reset()
    return () => {}
  }, [field.value])
  return (
    <div className='mb-3'>
      <ReCAPTCHA
        ref={recaptchaRef}
        hl={isFr ? 'fr' : 'en'}
        // size='invisible'
        sitekey='6LdmMj0aAAAAAIvn1bIItA3cbg90sgOCadNjbRTU'
        onChange={e => {
          // const token = await recaptchaRef.current.executeAsync()
          console.log('recaptcha updated', e)
          helpers.setValue(true)
        }}
      />
      <Validation>{meta.error}</Validation>
    </div>
  )
}

const Validation = styled.div`
  font-size: ${props => props.theme.font.size.m};
  color: ${props => props.theme.palette.darkSecondary};
  margin-top: 2px;
  height: 24px;
`
