import React from 'react'
import { useField } from 'formik'
import { useLocation } from '@reach/router'
import { Label, Input, Container, Validation, Radio } from './style'

interface IFormRadioProps {
  label?: string
  className?: string
  name: string
  required?: boolean
  data: {
    label: string
    labelFr: string
    value: string
  }[]
}

export default ({
  label,
  className,
  data,
  required = false,
  ...props
}: IFormRadioProps) => {
  const [field, meta, helpers] = useField(props)
  const loc = useLocation()
  const isFr = loc.pathname.startsWith('/fr/')

  return (
    <Container className={className}>
      <Label>
        {label} {required && <span>*</span>}
      </Label>
      {data.map(({ label, labelFr, value }) => {
        return (
          <Radio key={label}>
            {isFr ? labelFr : label}
            <input
              type='checkbox'
              checked={field.value && field.value.includes(value)}
              name={props.name}
              onChange={e => {
                if (e.target.checked) {
                  helpers.setValue([...field.value, value])
                } else {
                  helpers.setValue(field.value.filter((d: any) => d != value))
                }
              }}
            />
            <span className='checkmark'></span>
          </Radio>
        )
      })}
      <Validation visible={meta.touched && meta.error}>{meta.error}</Validation>
    </Container>
  )
}
